@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300;400;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kavoon&family=Noto+Sans:wght@300;400;800&display=swap");
html {
  font-size: 62.5%;
  box-sizing: inherit;
}

*, *:before, *:after {
  box-sizing: inherit;
}

Body {
  font-family: "Noto Sans", sans-serif;
  font-size: 2rem;
  line-height: 1.8rem;
}

p {
  color: black;
  font-size: 2rem;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a, li {
  text-decoration: none;
}

img, picture {
  display: block;
}

h1, h2, h3 {
  font-family: "Noto Sans", sans-serif;
  font-weight: 300;
  text-align: center;
  color: #000000;
}

