/* Media Queries*/
/*UI*/
.header {
  animation-duration: 3s;
  animation-name: nav;
  background-color: #FFFFFF;
  padding: 2rem 0 3rem 0;
}

@keyframes nav {
  from {
    transform: translate3d(0, -100%, 0);
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
.logo {
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .logo img {
    width: 50px;
    height: 50px;
  }
}

.content-header {
  display: flex;
  flex-direction: column;
}

.nav a,
.media_social a {
  margin: 1rem;
  color: #000000;
  font-weight: 400;
}
@media (max-width: 768px) {
  .nav a,
.media_social a {
    margin-top: 3rem;
    font-size: 1.5rem;
  }
}

.media_social {
  display: flex;
}
.media_social img {
  max-width: 30px;
  max-height: 30px;
}

.title {
  font-family: "Kavoon", cursive;
  font-size: 3rem;
}
@media (max-width: 768px) {
  .title {
    font-size: 2rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .nav {
    display: flex;
  }
}
.nav a {
  display: block;
}
.nav a:hover {
  color: #0e8d0a;
}

.content-footer {
  background-color: #e1e1e1;
  padding: 1.5rem;
}

.barra-footer {
  border-top: 1px solid red;
  border-bottom: 1px solid red;
}
.barra-footer .logo-footer {
  display: flex;
  justify-content: center;
}
.nav-social__footer {
  display: flex;
  justify-content: space-around;
  background-color: #e1e1e1;
}

@media (max-width: 768px) {
  .media_social a img {
    width: 25px;
    height: 25px;
  }
}

.info-footer {
  font-size: 1.5rem;
}

.titulo h1 {
  padding: 2rem;
  font-size: 5rem;
  font-weight: 400;
}

.cards {
  cursor: pointer;
  margin: 2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 5rem;
}
@media (max-width: 1024px) {
  .cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 768px) {
  .cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 375px) {
  .cards {
    display: grid;
    grid-template-columns: 1fr;
  }
}
.cards .content-card {
  border: 1px solid #e1e1e1;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: transform 0.2s;
}
.cards .content-card:hover {
  transform: scale(1.1);
}
.cards .img-card {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.iconos ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin-top: 1rem;
}

.card-texto {
  text-align: center;
}
.card-texto h2 {
  font-size: 2.5rem;
  font-weight: normal;
  color: #333333;
}
.card-texto p {
  font-size: 1.5rem;
}
.card-texto .precio {
  color: #0e8d0a;
}
.card-texto .precio span {
  font-weight: bold;
}

.button-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: inline-block;
  border: none;
}
.button-amarillo:hover {
  background-color: #c77808;
  color: #FFFFFF;
  cursor: pointer;
}

.button-amarillo__block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: block;
  border: none;
}
.button-amarillo__block:hover {
  background-color: #c77808;
  color: #FFFFFF;
  cursor: pointer;
}

.button-verde {
  background-color: #0e8d0a;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: block;
  border: none;
}
.button-verde:hover {
  background-color: #0c7508;
  color: #FFFFFF;
  cursor: pointer;
}

.ver-todas {
  display: flex;
  justify-content: flex-end;
  margin-top: 2rem;
}

.boton-rojo-block {
  background-color: rgb(248, 6, 6);
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #df0505;
  color: #FFFFFF;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  color: #FFFFFF;
  cursor: pointer;
}

.previaje {
  background-image: url("../../../build/img/previaje.webp");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  margin-top: 5rem;
  height: 25rem;
}

.promos {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.promos picture {
  max-width: 300px;
  max-height: 330px;
  overflow: inherit;
  object-fit: contain;
}

/*CARROUSELL*/
.banner img {
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
}

.banner-text {
  animation-duration: 4s;
  animation-name: texto;
}
.banner-text h5 {
  text-transform: uppercase;
}
.banner-text h5, .banner-text p {
  margin-bottom: 5rem;
  font-family: "Noto Sans", sans-serif;
  font-size: 3.5rem;
  font-weight: bold;
  color: #ffffff;
  -webkit-text-stroke: 0.5px black;
}
@media (max-width: 800px) {
  .banner-text h5, .banner-text p {
    font-size: 2rem;
  }
}

@keyframes texto {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}